import { HttpClient } from '@angular/common/http';
import { AngularFirestore } from '@angular/fire/firestore';
import { Observable } from 'rxjs';

export abstract class FirebaseApiBase<T> {
  protected constructor(
    protected http: HttpClient,
    protected collectionName: string,
    protected firestore: AngularFirestore
  ) {}

  public getAll(): Observable<T[]> {
    return this.firestore
      .collection(this.collectionName)
      .valueChanges() as Observable<T[]>;
  }

  public set(item: T): Promise<any> {
    return this.firestore.collection(this.collectionName).add(item);
  }

  public update(id: string, item: T): Promise<any> {
    return this.firestore.collection(this.collectionName).doc(id).update(item);
  }

  public delete(id: string): Promise<any> {
    return this.firestore.collection(this.collectionName).doc(id).delete();
  }
}
